	module.exports = {"data":{"post":{"html":"<p>Washington Post: <a href=\"http://www.washingtonpost.com/wp-dyn/content/article/2006/02/20/AR2006022000595.html\" title=\"Heros Files for Bankruptcy Amid Probe\">Heros Files for Bankruptcy Amid Probe</a> \"Heros, a leading German money-transport company, filed for bankruptcy on Monday as authorities announced an investigation of suspected large-scale embezzlement... estimated (at) 300 million euros ($358 million)... (It is) estimated that Heros had a market share of about 50 percent.\" So, if you run any type of retail establishment in Germany, as of today you have no way to get your money to the bank. That's why Mama is working late tonight (although I'm not sure what she can do, buy a fleet of armored cars on EBay?).</p>\n<p><strong>Update:</strong> Here's a good one: deposits that were picked up by Heros over the weekend are being held pending the bankruptcy settlement. So that money you thought was in the bank on Monday actually isn't. Even if your money wasn't embezzled, you still lose.</p>","fields":{"title":"If You Can't Trust Your Armored Car Company...","date":"20 Feb 2006","url":"https://papascott.de/2006-02-20-if-you-cant-trust-your-armored-car-company/"}}},"pathContext":{"slug":"/2006-02-20-if-you-cant-trust-your-armored-car-company/","prev":{"fields":{"title":"","date":"19 Feb 2006","slug":"/2006-02-19-sex-goddess-tanja-hoetzle/"}},"next":{"fields":{"title":"Two Great Hypes That Go Great Together","date":"21 Feb 2006","slug":"/2006-02-21-two-great-hypes-that-go-great-together/"}}}}

